.firstInfo {
  width: 100%;
  display: flex;
  align-self: center;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 40px;
  padding: 0 10%;
}

.shortAboutMe img {
  width: 100%;
}
.myName {
  font-size: 100px;
  color: white;
  font-weight: 800;
  word-wrap: break-word;
  flex-wrap: wrap;
  align-self: center;
  transition: 0.3s;
}
.shortAboutMe {
  color: white;
  word-wrap: break-word;
  max-width: 400px;
  align-self: flex-end;
  font-size: 18px;
}
.shortAboutMe h2 {
  border-bottom: 2px solid #c04f1b;
}

.animatedDot {
  position: absolute;
  background-color: #fff;
  border-radius: 1px;
  width: 2px;
  height: 2px;
  color: white;
}

@media (max-width: 1100px) {
  .firstInfo {
    width: 100%;
  }
}

@media (max-width: 750px) {
  .myName {
    font-size: 70px;
  }
}
@media (max-width: 450px) {
  .myName {
    font-size: 50px;
  }
}

/* SECOND INFO BUGGED SO I PUT IT HERE */

.secondInfo {
  width: 100%;
  display: flex;
  align-self: center;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 40px;
  background-color: rgb(26, 26, 26);
  padding: 3% 10%;
}
.secondInfo::before {
  background-color: rgba(255, 255, 255, 0.5);
}

.aboutMe {
  width: 400px;
  font-size: larger;
  height: fit-content;
  padding: 2px 4px;
  border-radius: 5px;
}
.aboutMe img {
  width: 100%;
}

.aboutMeParagraph::selection {
  color: white;
}
.aboutMeParagraph {
  color: #c04f1b;
  transition: 0.2s;
  text-align: center;
}
.questionAndAnswer {
  display: flex;
  flex-direction: column;
  padding: 15px 10px;
  border-radius: 10px;
}
.questionAndAnswer:nth-child(2n) {
  background-color: rgb(31, 31, 31);
}

.javascript {
  transition: 0.3s;
}

.javascript:hover {
  color: #f0db4f;
}

.questionAndAnswer .question {
  color: #c04f1b;
  text-decoration: dotted;
  margin-right: 5px;
}

.questionAndAnswer .question::selection {
  color: white;
}

.mySkills {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 360px;
  align-items: center;
  padding: 2px 4px;
  border-radius: 5px;
  align-self: center;
  text-align: center;
}
.mySkills img {
  width: 100%;
}
.mySkills img:nth-child(2n) {
  width: 100%;
  background-color: rgb(31, 31, 31);
  padding: 15px;
  border-radius: 10px;
}
.aboutMeParagraph:hover {
  color: #eb6121;
}

@media (max-width: 1100px) {
  .secondInfo {
    width: 100%;
  }
}
