.myProjectsContainer {
  display: flex;
  align-self: center;
  justify-content: space-around;
  flex-wrap: wrap;
  flex-direction: column;
  margin-top: 40px;
  margin: 3% 10%;
  font-size: 20px;
  transition: 1s;
}

.myProjectsContainer h2 {
  text-align: center;
}
.project {
  width: 500px;
  padding: 15px;
  border-radius: 10px;
}
.project:nth-child(2n) {
  background-color: rgb(26, 26, 26);
}

.project img {
  width: 100%;
  transition: 0.3s;
  max-height: 390px;
}
.project img:hover {
  box-shadow: 0 0 10px white;
}
.projectName {
  text-align: center;
}
.description {
  text-align: center;
}
.myProjects {
  display: flex;
  width: 100%;
  justify-content: space-around;
  flex-wrap: wrap;
}

@media (max-width: 500px) {
  .myProjectsContainer {
    width: 100%;
  }
}
